<template>
    <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="date" label="" width="180">
        </el-table-column>
        <el-table-column prop="name" label="" width="180">
        </el-table-column>
        <el-table-column prop="address" label="">
        </el-table-column>
        <el-table-column prop="address" label="">
      <template scope="scope">
        <el-button
          size="small"
          @click="handleEdit(scope.$index, scope.row)"></el-button>
        <el-button
          size="small"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)"></el-button>
      </template>
        </el-table-column>
    </el-table>
</template>
<script>
export default {
    data() {
        return {
            tableData: [{
                date: '2016-05-02',
                name: '',
                address: ' 1518 '
            }, {
                date: '2016-05-04',
                name: '',
                address: ' 1517 '
            }, {
                date: '2016-05-01',
                name: '',
                address: ' 1519 '
            }, {
                date: '2016-05-03',
                name: '',
                address: ' 1516 '
            }]
        }
    }
}
</script>



