<template>
    <div class="pricing">
        <h1></h1>
        <p></p>
        <div class="cards">
            <div class="card">
                <span></span>
                <p> 0/</p>
            </div>
            <div class="card">
                <span>Plus</span>
                <p> 20/</p>
            </div>
            <div class="card">
                <span>Pro</span>
                <p> 35/</p>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
            return {
                mask: {
                    display: 'none'
                },
                showIndex: true,
                showBg: false
            }
        },
        methods: {
            urlChange: function(r) {
                console.log(r)
            }
        },
}
</script>
<style>
.pricing {
    margin-top: 50px;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    width: 980px;
}

.pricing > p {
    font-size: 14px;
}

.pricing .cards {
    display: inline-block;
    height: 600px;
    margin-right: auto;
    margin-left: auto;
    margin-top: 20px;
}

.pricing .cards .card {
    width: 200px;
    height: 600px;
    float: left;
    background-color: #fff
}
</style>



